import * as React from "react";
import ReactDOM from "react-dom";
import VariablesForm from "../Calculators/VariablesForm";
import CompoundInterestChart from "../Calculators/CompoundInterestChart";
import defaultState from "../Calculators/defaultState";
export const CompoundCal = () => {
  const [state, setState] = React.useState(defaultState);

  return (
    <div id="annualCompoundCalculator">
      <h1 className="text-center">Annual Compound Interest Calculator</h1>
      <hr />
      <VariablesForm onUpdate={(variables) => setState(variables)} />
      <hr />
      <CompoundInterestChart {...state} />
    </div>
  );
};

export default CompoundCal;
